import { Commit } from 'vuex'
const state: any = {
  isCn: false,
  isLogin: false,
  headLogonState: true,
  tab: 1,
  socialArr: [
    {
      media: 'twitter',
      link_cn: 'https://twitter.com/BitpieWallet',
      link_en: 'https://twitter.com/BitpieWallet',
      icon: require('@/assets/twitter.png')
    },
    {
      media: 'telegram',
      link_cn: 'https://t.me/BitpieInternational',
      link_en: 'https://t.me/BitpieInternational',
      icon: require('@/assets/telegram.png')
    },
    {
      media: 'medium',
      link_cn: 'https://medium.com/@Bitpie',
      link_en: 'https://medium.com/@Bitpie',
      icon: require('@/assets/medium.png')
    },
    {
      media: 'reddit',
      link_cn: 'https://www.reddit.com/r/BitpieWallet/',
      link_en: 'https://www.reddit.com/r/BitpieWallet/',
      icon: require('@/assets/reddit.png')
    }
  ]
}

const getters: any = {
  getIsCn: () => state.isCn,
  getIsLogin: () => state.isLogin,
  getTab: () => state.tab
}

const mutations: any = {
  changeLocalLan(states: any, lan: string) {
    if (lan == 'cn') {
      states.isCn = true
    } else {
      states.isCn = false
    }
  },
  changeLoginState(states: any, login: boolean) {
    states.isLogin = login
  },
  changeTab(states: any, tab: number) {
    states.tab = tab
  },
  headLogonState(states: any, isLoginClick: boolean) {
    states.headLogonState = isLoginClick
  }
}

const actions: any = {
  changeLocalLan(context: { commit: Commit }, lan: string) {
    context.commit('changeLocalLan', lan)
  },
  changeLoginState(context: { commit: Commit }, login: boolean) {
    context.commit('changeLoginState', login)
  },
  changeTab(context: { commit: Commit }, tab: number) {
    context.commit('changeTab', tab)
  },
  headLogonState(context: { commit: Commit }, isLoginClick: boolean) {
    context.commit('headLogonState', isLoginClick)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
