




























































































































































































































































































































































































































































































import { Component, Vue, Watch } from "vue-property-decorator";
import { namespace, State, Action } from "vuex-class";
import axios from "axios";
import network from "@/utils/network";
import "echarts/lib/chart/line";
import "echarts/lib/component/tooltip";
import "@/style/slider.scss";
import "@/style/custom.scss";
import common from "../../store/common/common";
const Common = namespace("common");
import EOSClass from "eosjs";
import "@/utils/date";
var _ = require("lodash");

@Component({})
export default class Home extends Vue {
  @Common.State("isLogin") isLogin!: boolean;
  @Common.State("tab") tab!: number;
  @Common.State("headLogonState") headLogonState!: boolean;
  @Common.Action changeLoginState!: (login: boolean) => any;
  @Common.Action changeTab!: (tabNumber: number) => any;
  scatter: any = null;
  account: any = { name: null, authority: null };
  eos: any = null;
  rexBalance: string = "0 REX";
  rexEquivalent: string = "0.0000 EOS";
  eosBalance: string = "0.0000 EOS";
  rexFund: string = "0.0000 EOS";
  depositRexFund: string = "";
  withdrawRexFund: string = "";
  tipsRead: number = 0;
  rexTotal: string = "0 REX";
  eosTotal: string = "0.0000 EOS";
  eosLent: string = "0.0000 EOS";
  eosLendable: string = "0.0000 EOS";
  loansNumber: number = 0;
  resourcePrice: string = "0";
  percentLent: string = "0%";
  ramIncome: string = "0.0000 EOS";
  bidIncome: string = "0.0000 EOS";
  rentIncome: string = "0.0000 EOS";
  estimated: string = "0%";
  stakeList: [] = [];
  rentList: any[] = [];
  unstakeVisible: boolean = false;
  unstakeItem = null;
  unstakeCpu = "0";
  unstakeNet = "0";
  isVoted: boolean = false;
  buyNumber: string = "0";
  sellNumber: string = "0";
  rexMaturities: [] = [];
  maturitiesVisible: boolean = false;
  saveVisible: boolean = false;
  saveRex: string = "0";
  unstakeRex: string = "0";
  maturedRex: number = 0;
  rexCurrentPrice: number = 0;
  radioType: string = "cpu";
  lentAccount: String = "";
  lentAmount: String = "";
  dwAmount: String = "";
  lentResourceAmount: String = "";
  isShowRentFund: boolean = false;
  rentObject: any = null;
  isDeposit: Boolean = true;
  tooltip = {
    trigger: "axis",
    confine: true,
    axisPointer: {
      animation: false,
      type: "cross",
      lineStyle: {
        color: "#999",
        width: 1,
        opacity: 0.5
      }
    }
  };

  axisPointer = {
    link: {
      xAxisIndex: "all"
    },
    label: {
      backgroundColor: "#333"
    }
  };

  xAxis = {
    data: [],
    type: "category",
    axisLabel: {
      color: "rgba(153,153,153,0.5)"
    },
    nameTextStyle: {
      color: "rgba(153,153,153,0.5)"
    }
  };

  yAxis = {
    name: "EOS / REX",
    type: "value",
    scale: true,
    splitLine: {
      show: true,
      lineStyle: {
        opacity: 0.1,
        color: "#999"
      }
    },
    axisLabel: {
      color: "rgba(153,153,153,0.5)"
    },
    nameTextStyle: {
      color: "rgba(153,153,153,0.5)"
    }
  };

  grid = {
    top: 26,
    left: 95,
    right: 20,
    bottom: 30
  };

  series = {
    type: "line",
    smooth: true,
    data: [],
    lineStyle: {
      color: "#459df5",
      opacity: 0.8
    },
    showSymbol: false
  };

  line = {};

  mounted() {
    this.eos = new EOSClass({
      httpEndpoint: `${network.protocol}://${network.host}:${network.port}`,
      chainId: network.chainId,
      expireInSeconds: 10
    });
    this.getScatter();
    if (this.$cookie.get("tips")) {
      this.tipsRead = this.$cookie.get("tips");
    }
    if (this.tab == 0) {
      this.getRexChart();
    } else if (this.tab == 1) {
      this.getRentChart();
    }
    this.getIncome();
    this.getRexPool();
  }

  dwClick() {
    let payAmount = Number(this.dwAmount).toFixed(4) + " EOS";
    if (!this.loginVerify()) {
      return;
    }
    let authorization = {
      authorization: this.account.name + "@" + this.account.authority
    };
    let eos = this.scatter.eos(network, EOSClass, {});
    eos
      .contract("eosio")
      .then(contract => {
        return contract.transaction(tr => {
          if (this.rentObject.type == "cpu") {
            if (this.isDeposit) {
              tr.fundcpuloan(
                this.account.name,
                this.rentObject.loan_num,
                payAmount,
                authorization
              );
            } else {
              tr.fundnetloan(
                this.account.name,
                this.rentObject.loan_num,
                payAmount,
                authorization
              );
            }
          } else {
            if (this.isDeposit) {
              tr.defcpuloan(
                this.account.name,
                this.rentObject.loan_num,
                payAmount,
                authorization
              );
            } else {
              tr.defnetloan(
                this.account.name,
                this.rentObject.loan_num,
                payAmount,
                authorization
              );
            }
          }
        });
      })
      .then(result => {
        setTimeout(() => this.getRequest(), 1000);
        this.$message({
          showClose: true,
          message: "success",
          type: "success",
          duration: 2000
        });
      })
      .catch(err => {
        console.log(err);
        this.$message({
          showClose: true,
          message: err,
          type: "error",
          duration: 2000
        });
      });
  }

  rentOperationClick(isDeposit, object) {
    this.isDeposit = isDeposit;
    this.rentObject = object;
    this.isShowRentFund = true;
  }

  rentClick() {
    let payAmount = Number(this.lentAmount).toFixed(4) + " EOS";
    let rexFund = this.lentResourceAmount
      ? Number(this.lentResourceAmount).toFixed(4) + " EOS"
      : "0.0000 EOS";
    if (!this.loginVerify()) {
      return;
    }
    let authorization = {
      authorization: this.account.name + "@" + this.account.authority
    };
    let eos = this.scatter.eos(network, EOSClass, {});
    eos
      .contract("eosio")
      .then(contract => {
        return contract.transaction(tr => {
          if (this.radioType == "cpu") {
            authorization;
            tr.rentcpu(
              this.account.name,
              this.lentAccount,
              payAmount,
              rexFund,
              authorization
            );
          } else {
            tr.rentnet(
              this.account.name,
              this.lentAccount,
              payAmount,
              rexFund,
              authorization
            );
          }
        });
      })
      .then(result => {
        setTimeout(() => this.getRequest(), 1000);
        this.$message({
          showClose: true,
          message: "success",
          type: "success",
          duration: 2000
        });
      })
      .catch(err => {
        console.log(err);
        this.$message({
          showClose: true,
          message: err,
          type: "error",
          duration: 2000
        });
      });
  }

  getRentList() {
    this.rentList = [];
    let netParams = {
      code: "eosio",
      index_position: 3,
      json: true,
      key_type: "name",
      limit: 100,
      lower_bound: this.account.name,
      scope: "eosio",
      table: "netloan",
      table_key: "",
      upper_bound: this.account.name
    };
    let cpuParams = {
      code: "eosio",
      index_position: 3,
      json: true,
      key_type: "name",
      limit: 100,
      lower_bound: this.account.name,
      scope: "eosio",
      table: "cpuloan",
      table_key: "",
      upper_bound: this.account.name
    };
    this.eos
      .getTableRows(netParams)
      .then(response => {
        response.rows.forEach(element => {
          element.type = "net";
          element.expire = new Date(element.expiration).Format("MM/dd hh:mm");
          this.rentList.push(element);
        });
      })
      .catch(error => {});

    this.eos
      .getTableRows(cpuParams)
      .then(response => {
        response.rows.forEach(element => {
          element.type = "cpu";
          element.expire = new Date(element.expiration).Format("MM/dd hh:mm");
          this.rentList.push(element);
        });
      })
      .catch(error => {});
  }

  getStakeNumber() {
    let stakeAble = this.maturedRex;
    let staked = 0;
    for (let i = 0; i < this.rexMaturities.length; i++) {
      let item: { time: string; rex: string } = this.rexMaturities[i];
      let rexArr = item.rex.split(" ");
      if (item.time != this.$t("exchange.rex_stake_title")) {
        stakeAble = parseFloat(rexArr[0]) + stakeAble;
      } else {
        staked = parseFloat(rexArr[0]) + staked;
      }
    }
    return { stakeAble: stakeAble.toFixed(4), staked: staked.toFixed(4) };
  }

  tipsClick() {
    this.tipsRead = 1;
    this.$cookie.set("tips", "1", 10000);
  }

  tabClick(tabNumber: number) {
    if (tabNumber == 0) {
      this.getRexChart();
    } else if (tabNumber == 1) {
      this.getRentChart();
    }
    this.changeTab(tabNumber);
  }

  unstakeDialogClick(item) {
    this.unstakeItem = item;
    this.unstakeCpu = "0";
    this.unstakeNet = "0";
    this.unstakeVisible = true;
  }

  unstakeClick() {
    if (!this.loginVerify()) {
      return;
    }
    if (this.isVoted) {
      if (
        parseFloat(this.unstakeCpu) >= 0 &&
        parseFloat(this.unstakeCpu) >= 0
      ) {
        let eos = this.scatter.eos(network, EOSClass, {});
        let authorization = {
          authorization: this.account.name + "@" + this.account.authority
        };
        eos
          .contract("eosio")
          .then(contract => {
            return contract.transaction(tr => {
              tr.unstaketorex(
                this.account.name,
                this.unstakeItem.to,
                parseFloat(this.unstakeNet).toFixed(4) + " EOS",
                parseFloat(this.unstakeCpu).toFixed(4) + " EOS",
                authorization
              );
            });
          })
          .then(result => {
            this.unstakeVisible = false;
            setTimeout(() => this.getRequest(), 1000);
            this.$message({
              showClose: true,
              message: "success",
              type: "success",
              duration: 2000
            });
          })
          .catch(err => {
            this.$message({
              showClose: true,
              message: err,
              type: "error",
              duration: 2000
            });
          });
      } else {
        this.$message({
          showClose: true,
          message: this.$t("common.min_limit").toString(),
          type: "warning",
          duration: 2000
        });
      }
    } else {
      this.$message({
        showClose: true,
        message: this.$t("common.vote_tips").toString(),
        type: "warning",
        duration: 2000
      });
    }
  }

  buyRexClick() {
    if (!this.loginVerify()) {
      return;
    }
    if (this.isVoted) {
      let rexFundArr = this.rexFund.split(" ");
      let rexFundNumber = parseFloat(rexFundArr[0]);
      if (
        parseFloat(this.buyNumber) >= 0 &&
        parseFloat(this.buyNumber) <= rexFundNumber &&
        this.account.name
      ) {
        let eos = this.scatter.eos(network, EOSClass, {});
        let amount = parseFloat(this.buyNumber).toFixed(4) + " EOS";
        let authorization = {
          authorization: this.account.name + "@" + this.account.authority
        };

        eos
          .contract("eosio")
          .then(contract => {
            return contract.transaction(tr => {
              tr.buyrex(this.account.name, amount, authorization);
            });
          })
          .then(res => {
            this.buyNumber = "0";
            setTimeout(() => this.getRequest(), 1000);
            this.$message({
              showClose: true,
              message: "success",
              type: "success",
              duration: 2000
            });
          })
          .catch(err => {
            this.$message({
              showClose: true,
              message: err,
              type: "error",
              duration: 2000
            });
          });
      } else {
        this.$message({
          showClose: true,
          message: this.$t("common.min_limit").toString(),
          type: "warning",
          duration: 2000
        });
      }
    } else {
      this.$message({
        showClose: true,
        message: this.$t("common.vote_tips").toString(),
        type: "warning",
        duration: 2000
      });
    }
  }

  sellRexClick() {
    if (!this.loginVerify()) {
      return;
    }
    let rexArr = this.rexBalance.split(" ");
    let rexNumber = parseFloat(rexArr[0]);
    if (
      parseFloat(this.sellNumber) >= 0 &&
      parseFloat(this.sellNumber) <= rexNumber &&
      this.account.name
    ) {
      let eos = this.scatter.eos(network, EOSClass, {});
      let amount = parseFloat(this.sellNumber).toFixed(4) + " REX";
      let authorization = {
        authorization: this.account.name + "@" + this.account.authority
      };

      eos
        .contract("eosio")
        .then(contract => {
          return contract.transaction(tr => {
            tr.sellrex(this.account.name, amount, authorization);
          });
        })
        .then(res => {
          this.sellNumber = "0";
          setTimeout(() => this.getRequest(), 1000);
          this.$message({
            showClose: true,
            message: "success",
            type: "success",
            duration: 2000
          });
        })
        .catch(err => {
          console.log("err", err);
          this.$message({
            showClose: true,
            message: err,
            type: "error",
            duration: 2000
          });
        });
    } else {
      this.$message({
        showClose: true,
        message: this.$t("common.min_limit").toString(),
        type: "warning",
        duration: 2000
      });
    }
  }

  rexLockClick() {
    this.maturitiesVisible = true;
  }

  saveClick() {
    this.saveVisible = true;
  }

  rexSaveClick() {
    if (!this.loginVerify()) {
      return;
    }
    let rexStake = this.getStakeNumber();
    if (
      parseFloat(this.saveRex) >= 0 &&
      parseFloat(this.saveRex) <= parseFloat(rexStake.stakeAble) &&
      this.account.name
    ) {
      let eos = this.scatter.eos(network, EOSClass, {});
      let amount = parseFloat(this.saveRex).toFixed(4) + " REX";
      let authorization = {
        authorization: this.account.name + "@" + this.account.authority
      };

      eos
        .contract("eosio")
        .then(contract => {
          return contract.transaction(tr => {
            return tr.mvtosavings(this.account.name, amount, authorization);
          });
        })
        .then(res => {
          this.saveRex = "0";
          setTimeout(() => this.getRequest(), 1000);
          this.$message({
            showClose: true,
            message: "success",
            type: "success",
            duration: 2000
          });
        })
        .catch(err => {
          console.log("err", err);
          this.$message({
            showClose: true,
            message: err,
            type: "error",
            duration: 2000
          });
        });
    } else {
      this.$message({
        showClose: true,
        message: this.$t("common.min_limit").toString(),
        type: "warning",
        duration: 2000
      });
    }
  }

  rexUnstakeClick() {
    if (!this.loginVerify()) {
      return;
    }
    let rexStake = this.getStakeNumber();
    if (
      parseFloat(this.unstakeRex) >= 0 &&
      parseFloat(this.unstakeRex) <= parseFloat(rexStake.staked) &&
      this.account.name
    ) {
      let eos = this.scatter.eos(network, EOSClass, {});
      let amount = parseFloat(this.unstakeRex).toFixed(4) + " REX";
      let authorization = {
        authorization: this.account.name + "@" + this.account.authority
      };

      eos
        .contract("eosio")
        .then(contract => {
          return contract.transaction(tr => {
            return tr.mvfrsavings(this.account.name, amount, authorization);
          });
        })
        .then(res => {
          this.unstakeRex = "0";
          setTimeout(() => this.getRequest(), 1000);
          this.$message({
            showClose: true,
            message: "success",
            type: "success",
            duration: 2000
          });
        })
        .catch(err => {
          console.log("err", err);
          this.$message({
            showClose: true,
            message: err,
            type: "error",
            duration: 2000
          });
        });
    } else {
      this.$message({
        showClose: true,
        message: this.$t("common.min_limit").toString(),
        type: "warning",
        duration: 2000
      });
    }
  }

  getRexPool() {
    let params = {
      code: "eosio",
      json: true,
      scope: "eosio",
      table: "rexpool"
    };
    this.eos
      .getTableRows(params)
      .then(response => {
        if (response.rows[0]) {
          let rexPool = response.rows[0];
          this.rexTotal = rexPool.total_rex;
          this.eosLent = rexPool.total_lent;
          this.eosLendable = rexPool.total_unlent;
          this.eosTotal = rexPool.total_lendable;
          this.loansNumber = rexPool.loan_num;
          let lentArr = rexPool.total_lent.split(" ");
          let totalLent = lentArr[0];
          let lendableArr = rexPool.total_lendable.split(" ");
          let totalLendable = lendableArr[0];
          let percent = (totalLent / totalLendable).toFixed(4);
          this.percentLent = `${parseFloat(percent) * 100} %`;
          let rentArr = rexPool.total_rent.split(" ");
          let totalRent = rentArr[0];
          this.rentIncome = `${(parseFloat(totalRent) - 2e4).toFixed(4)} EOS`;
          this.resourcePrice = (
            (1 * parseFloat(rexPool.total_unlent)) /
            (parseFloat(rexPool.total_rent) + 1)
          ).toFixed(4);
          let n =
            (parseFloat(this.eosTotal) / parseFloat(this.rexTotal) - 1e-4) /
            1e-4;
          let date = new Date("2019-05-01T12:47:00.000Z");
          let a =
            new Date().getTime() -
            new Date(date.Format("yyyy/MM/dd hh:mm:ss")).getTime();
          this.estimated = `${(
            (n / a) *
            365 *
            24 *
            60 *
            60 *
            1e3 *
            100
          ).toFixed(2)} %`;
        }
      })
      .catch(error => {
        setTimeout(() => this.getRexPool(), 3000);
      });
  }

  getIncome() {
    axios
      .get("https://betaserver.mytokenpocket.vip/v1/rex/income?blockchain_id=4")
      .then(response => {
        if (response.status == 200) {
          this.ramIncome = `${parseFloat(
            response.data.data["eosio.ramfee"]
          ).toFixed(4)} EOS`;
          this.bidIncome = `${parseFloat(
            response.data.data["eosio.names"]
          ).toFixed(4)} EOS`;
        }
      })
      .catch(error => {
        setTimeout(() => this.getIncome(), 3000);
      });
  }

  getRentChart() {
    axios
      .get(
        "https://tbeosapi1.mytokenpocket.vip/v1/token/30?token=t_eosio_rexrentprod"
      )
      .then(response => {
        if (response.status == 200) {
          let resultArr = new Array().concat(response.data);
          let dateArr = resultArr.map(data => {
            let date = new Date(data.dt);
            return date.Format("MM-dd hh:mm");
          });
          this.xAxis.data = dateArr;
          let seriesArr = resultArr.map(data => {
            return [
              parseFloat(data.o).toFixed(1),
              parseFloat(data.c).toFixed(1),
              parseFloat(data.l).toFixed(1),
              parseFloat(data.h).toFixed(1)
            ];
          });
          this.series.data = seriesArr.map(data => {
            return data[0];
          });

          this.yAxis.name = "EOS * 30D / EOS";
          delete this.yAxis["min"];

          this.line = {
            animation: true,
            tooltip: this.tooltip,
            axisPointer: this.axisPointer,
            xAxis: this.xAxis,
            grid: this.grid,
            series: this.series,
            yAxis: this.yAxis
          };

          this.grid.left = 55;
        }
      })
      .catch(error => {
        setTimeout(() => this.getRentChart(), 3000);
      });
  }

  getRexChart() {
    axios
      .get(
        "https://tbeosapi1.mytokenpocket.vip/v1/token/30?token=t_eosio_rexprod"
      )
      .then(response => {
        if (response.status == 200) {
          let resultArr = new Array().concat(response.data);
          let dateArr = resultArr.map(data => {
            let date = new Date(data.dt);
            return date.Format("MM-dd hh:mm");
          });
          this.xAxis.data = dateArr;
          let seriesArr = resultArr.map(data => {
            return [
              parseFloat(data.o),
              parseFloat(data.c),
              parseFloat(data.l),
              parseFloat(data.h)
            ];
          });
          this.series.data = seriesArr.map(data => {
            return (1 / data[0]).toFixed(10);
          });

          this.rexCurrentPrice = _.last(this.series.data);

          let max = _.max(this.series.data);
          let min = _.min(this.series.data);

          this.yAxis.name = "EOS / REX";
          this.yAxis["min"] = Math.max(min - (max - min) / 5, 0);
          this.line = {
            animation: true,
            tooltip: this.tooltip,
            axisPointer: this.axisPointer,
            xAxis: this.xAxis,
            grid: this.grid,
            series: this.series,
            yAxis: this.yAxis
          };

          this.grid.left = 95;
        }
      })
      .catch(error => {
        setTimeout(() => this.getRexChart(), 3000);
      });
  }

  depositRexFundClick() {
    if (!this.loginVerify()) {
      return;
    }
    let balanceArr = this.eosBalance.split(" ");
    let maxAmount = balanceArr[0];
    if (parseFloat(this.depositRexFund) > parseFloat(maxAmount)) {
      this.$message({
        showClose: true,
        message: this.$t("common.max_deposit_limit").toString(),
        type: "warning",
        duration: 2000
      });
    } else if (parseFloat(this.depositRexFund) < 0.0001) {
      this.$message({
        showClose: true,
        message: this.$t("common.min_limit").toString(),
        type: "warning",
        duration: 2000
      });
    } else {
      let eos = this.scatter.eos(network, EOSClass, {});
      let quantity = `${parseFloat(this.depositRexFund).toFixed(4)} EOS`;
      let authorization = {
        authorization: this.account.name + "@" + this.account.authority
      };
      eos
        .contract("eosio")
        .then(result => {
          return result.deposit(this.account.name, quantity, authorization);
        })
        .then(result => {
          this.depositRexFund = "";
          setTimeout(() => {
            this.getRequest();
          }, 1000);
          this.$message({
            showClose: true,
            message: "success",
            type: "success",
            duration: 2000
          });
        })
        .catch(err => {
          console.log("err", err);
        });
    }
  }

  withdrawRexFundClick() {
    if (!this.loginVerify()) {
      return;
    }
    let balanceArr = this.rexFund.split(" ");
    let maxAmount = balanceArr[0];
    if (parseFloat(this.withdrawRexFund) > parseFloat(maxAmount)) {
      this.$message({
        showClose: true,
        message: this.$t("common.max_withdraw_limit").toString(),
        type: "warning",
        duration: 2000
      });
    } else if (parseFloat(this.withdrawRexFund) < 0.0001) {
      this.$message({
        showClose: true,
        message: this.$t("common.min_limit").toString(),
        type: "warning",
        duration: 2000
      });
    } else {
      let eos = this.scatter.eos(network, EOSClass, {});
      let quantity = `${parseFloat(this.withdrawRexFund).toFixed(4)} EOS`;
      let authorization = {
        authorization: this.account.name + "@" + this.account.authority
      };
      eos
        .contract("eosio")
        .then(result => {
          return result.withdraw(this.account.name, quantity, authorization);
        })
        .then(result => {
          this.withdrawRexFund = "";
          setTimeout(() => {
            this.getRequest();
          }, 1000);
          this.$message({
            showClose: true,
            message: "success",
            type: "success",
            duration: 2000
          });
        })
        .catch(err => {
          console.log("err", err);
        });
    }
  }

  getScatter() {
    this.$scatterJs.scatter.connect("pie-pocket").then(connected => {
      if (!connected) return false;
      let scatter = this.$scatterJs.scatter;
      this.scatter = scatter;
      let account;
      if (scatter.identity) {
        account = scatter.identity.accounts.find(
          account => account.blockchain === "eos"
        );
        this.account = account;
        if (account && account.name) {
          this.lentAccount = account.name;
        }
        this.getRequest();
      }
      if (!account || !account.name) {
        this.login();
      }
    });
  }

  getRequest() {
    this.getRexAsset();
    this.getEosBalance();
    this.getRexFund();
    this.getStakedList();
    this.getAccount();
    this.getRentList();
    if (this.$cookie.get(`${this.account.name}vote`)) {
      this.isVoted = this.$cookie.get(`${this.account.name}vote`);
    }
  }

  getRexFund() {
    console.log('this.rexFund ==========', this.rexFund);
    this.getRexFundBalance();
    this.getRexQueueBalance();
  }

  getRexFundBalance() {
    let rexFundParams = {
      code: "eosio",
      json: true,
      limit: 1,
      lower_bound: this.account.name,
      scope: "eosio",
      table: "rexfund",
      table_key: "",
      upper_bound: this.account.name
    };
    this.eos
      .getTableRows(rexFundParams)
      .then(response => {
        if (response.rows[0]) {
          let rexFundArr = this.rexFund.split(' ');
          let balanceArr = response.rows[0].balance.split(' ');
          this.rexFund = `${parseFloat(rexFundArr[0]) + parseFloat(balanceArr[0])} ${balanceArr[1]}`;
          console.log('this.rexFund ==========', this.rexFund);
        }
      })
      .catch(error => {
        setTimeout(() => this.getRexFundBalance(), 3000);
      });
  }

  getRexQueueBalance() {
    console.log('getRexQueue =========== getRexQueue');
    let rexQueueparams = {
      code: "eosio",
      json: true,
      limit: 10, 
      lower_bound: this.account.name,
      scope: "eosio",
      table: "rexqueue",
      table_key: "",
      upper_bound: this.account.name
    };

    this.eos
      .getTableRows(rexQueueparams)
      .then(response => {
        console.log('getRexQueue response=========== ', response);
        if (response.rows && response.rows[0] && response.rows[0].owner == this.account.name && response.rows[0].is_open == 0) {
          console.log('getRexQueue proceeds=========== ', response.rows[0].proceeds);
          let rexFundArr = this.rexFund.split(' ');
          let proceedsArr = response.rows[0].proceeds.split(' ');
          this.rexFund = `${parseFloat(rexFundArr[0])+ parseFloat(proceedsArr[0])} ${proceedsArr[1]}`;
          console.log('this.rexFund ==========', this.rexFund);
        }
      })
      .catch(error => {
        setTimeout(() => this.getRexQueueBalance(), 3000);
      });
  }

  getEosBalance() {
    if (!this.loginVerify()) {
      return;
    }
    let params = {
      account: this.account.name,
      code: "eosio.token",
      symbol: "EOS"
    };
    this.eos
      .getCurrencyBalance(params)
      .then(response => {
        this.eosBalance = response[0];
      })
      .catch(error => {
        setTimeout(() => this.getEosBalance(), 3000);
      });
  }

  getRexAsset() {
    if (!this.loginVerify()) {
      return;
    }
    let params = {
      code: "eosio",
      json: true,
      limit: 1,
      lower_bound: this.account.name,
      scope: "eosio",
      table: "rexbal",
      table_key: "",
      upper_bound: this.account.name
    };
    this.eos
      .getTableRows(params)
      .then(response => {
        if (response.rows[0]) {
          this.rexBalance = response.rows[0].rex_balance;
          this.rexEquivalent = response.rows[0].vote_stake;
          let tempArr = response.rows[0].rex_maturities;
          this.maturedRex =
            response.rows[0].matured_rex > 0
              ? parseFloat((response.rows[0].matured_rex / 1e4).toFixed(4))
              : 0;
          this.rexMaturities = tempArr.map(t => {
            let date = new Date(t.first);
            let now = new Date();
            let nowTimestamp = now.getTime();
            let rexDate = date.Format("yyyy/MM/dd hh:mm:ss");
            let rexTimestamp = new Date(rexDate).getTime();
            if (nowTimestamp > rexTimestamp) {
              this.maturedRex =
                this.maturedRex + parseFloat((t.second / 1e4).toFixed(4));
            }
            if (
              parseInt(date.Format("yyyyMMdd")) > parseInt(this.getDateStr(5))
            ) {
              let rex = `${(t.second / 10000).toFixed(4)} REX`;
              return { time: this.$t("exchange.rex_stake_title"), rex: rex };
            } else {
              let rex = `${(t.second / 10000).toFixed(4)} REX`;
              return { time: date.Format("MM-dd hh:mm"), rex: rex };
            }
          });
        }
      })
      .catch(error => {
        setTimeout(() => this.getRexAsset(), 3000);
      });
  }

  getStakedList() {
    if (!this.loginVerify()) {
      return;
    }
    let params = {
      code: "eosio",
      json: true,
      limit: 10,
      scope: this.account.name,
      table: "delband"
    };
    this.eos
      .getTableRows(params)
      .then(response => {
        if (response.rows[0]) {
          this.stakeList = response.rows;
        }
      })
      .catch(error => {
        setTimeout(() => this.getStakedList(), 3000);
      });
  }

  getDateStr(AddDayCount) {
    var dd = new Date();
    dd.setDate(dd.getDate() + AddDayCount);
    var y = dd.getFullYear();
    var m =
      dd.getMonth() + 1 < 10 ? "0" + (dd.getMonth() + 1) : dd.getMonth() + 1;
    var d = dd.getDate() < 10 ? "0" + dd.getDate() : dd.getDate();
    return `${y}${m}${d}`;
  }

  login() {
    if (this.scatter) {
      this.scatter
        .getIdentity({
          accounts: [network]
        })
        .then(() => {
          let account = this.scatter.identity.accounts.find(
            account => account.blockchain === "eos"
          );
          if (!account) return;
          this.account = account;
          if (account && account.name) {
            this.lentAccount = account.name;
          }
          this.getRequest();
        })
        .catch(e => {
          this.$message({
            showClose: true,
            message: e.message,
            type: "warning",
            duration: 2000
          });
        });
    }
  }

  loginVerify() {
    if (!this.account.name) {
      this.$message({
        showClose: true,
        message: this.$t("common.need_login").toString(),
        type: "warning",
        duration: 2000
      });
      return false;
    }
    return true;
  }

  getAccount() {
    let eos = this.scatter.eos(network, EOSClass, {});
    if (eos && this.account.name) {
      eos
        .getAccount(this.account.name)
        .then(res => {
          if (res.voter_info.proxy || res.voter_info.producers.length >= 21) {
            this.isVoted = true;
            this.$cookie.set(`${this.account.name}vote`, "1", 10000);
          } else {
            this.isVoted = false;
          }
        })
        .catch(err => {
          this.isVoted = false;
        });
    }
  }

  logout() {
    this.scatter.forgetIdentity().then(() => {
      this.account = { name: null, authority: null };
      this.rexBalance = "0 REX";
      this.rexEquivalent = "0.0000 EOS";
      this.eosBalance = "0.0000 EOS";
      this.rexFund = "0.0000 EOS";
      this.stakeList = [];
      this.rentList = [];
    });
  }

  @Watch("account")
  onAccountChanged(val, oldValue) {
    if (val.name == null) {
      this.changeLoginState(false);
    } else if (val && val.name) {
      this.changeLoginState(true);
    }
  }
  @Watch("headLogonState")
  onLogonStateChanged(val, oldValue) {
    if (val != oldValue) {
      if (val) {
        this.login();
      } else {
        this.logout();
      }
    }
  }
}
