





















































































































import { Component, Vue } from "vue-property-decorator";
import { namespace, State, Action } from "vuex-class";
import common from "../../store/common/common";
const Common = namespace("common");

@Component
export default class Header extends Vue {
  isShowMenu: boolean = false;
  @Common.State("isCn") isCn!: boolean;
  @Common.State("isLogin") isLogin!: boolean;
  @Common.State("socialArr") socialArr!: [];
  @Common.Action changeLocalLan!: (lan: string) => any;
  @Common.Action headLogonState!: (isClick: boolean) => any;
  menuClick() {
    this.isShowMenu = !this.isShowMenu;
  }
  changeLan(lan: string) {
    this.changeLocalLan(lan);
    this.$i18n.locale = lan;
    this.isShowMenu = false;
  }
  socialClick(social: any) {
    if (this.isCn) {
      window.location.href = social.link_cn;
    } else {
      window.location.href = social.link_en;
    }
    this.isShowMenu = false;
  }
  changeLogonState(isLoginClick: boolean) {
    this.isShowMenu = false;
    this.headLogonState(isLoginClick);
  }
}
